<template>
  <div class="joke">
    <el-row class="text-center">
      <el-row v-for="item in jokes" :key="item.soureid">
        <el-row class="jokeText text-left line-h30">{{item.text}}</el-row>
        <video class="myVideo" v-if="item.type==='video'" :src="item.video" controls></video>
        <el-image class="p-10" v-if="item.type==='image'" :src="item.image" alt="">
          <div slot="placeholder" v-loading="true"></div>
          <div slot="error" class="image-slot">
            <img src="img/no_image.jpg" alt="" width="100%">
          </div>
        </el-image>
        <el-image class="p-10" v-if="item.type==='gif'" :src="item.gif" alt="">
          <div slot="placeholder" v-loading="true"></div>
          <div slot="error" class="image-slot">
            <img src="img/no_image.jpg" alt="" width="100%">
          </div>
        </el-image>
        <el-divider></el-divider>
      </el-row>
    </el-row>
    <el-row class='getMore p-40' v-loading="true">
      <!--<el-button type="primary" @click="getData">刷新</el-button>-->
      <!--<el-button type="primary" @click="getRealData">最新段子</el-button>-->
    </el-row>
    <el-row class="mobilePage">
      我只在小于400px的屏幕上显示
    </el-row>
  </div>
</template>
<script>
import { getRandomJoke } from '@/api/apiopen'

export default {
  name: 'joke',
  data () {
    return {
      jokes: [],
    }
  },
  created () {
    this.getJoke()
  },
  mounted () {
    this.$inView('.getMore').on('enter', () => {
      this.getJoke()
    }).on('exit', ()=>{})
  },
  methods: {
    getJoke () {
      getRandomJoke({
        param: {}
      }).then(res => {
        if (res.code === 200) {
          this.jokes = res.data
        } else {
          this.$message.error(res)
        }
      }).catch(error => {
        console.log(error)
      })
    }
  }
}
</script>
<style lang="scss">
.joke {
  background-color: #fff;

  .jokeText {
    width: 50%;
    max-width: 400px;
    margin: 0 auto;
    padding: 20px 0;
  }

  .myVideo{
    width: 100%;
    max-width: 400px;
  }

  .el-image {
    width: 100%;
    max-width: 400px;

    .image-slot {
      height: 50px;
    }
  }
}


@media screen and (max-device-width: 400px) {
  // 真实宽度小于400px (非web端窗口大小)
  .joke {
    .mobilePage {
      display: block;
    }
  }
}
</style>
